'use client'
import styles from './account.module.css'
import { useCallback, useEffect, useState } from 'react'
import { createClient } from '@/utils/supabase/client'
import { get_shoots } from './getshoots.js'
import { insert_shoots } from './shootsaction.js'
export default function AccountForm({ user }) {
  const supabase = createClient()
  const [loading, setLoading] = useState(true)
  const [fullname, setFullname] = useState(null)
  const [username, setUsername] = useState(null)
  const [website, setWebsite] = useState(null)
  const [avatar_url, setAvatarUrl] = useState(null)

  const getProfile = useCallback(async () => {
    try {
      setLoading(true)

      const { data, error, status } = await supabase
        .from('profiles')
        .select(`full_name, username, website, avatar_url`)
        .eq('id', user?.id)
        .single()

      if (error && status !== 406) {
        throw error
      }

      if (data) {
        setFullname(data.full_name)
        setUsername(data.username)
        setWebsite(data.website)
        setAvatarUrl(data.avatar_url)
      }
    } catch (error) {
      alert('Error loading user data!')
    } finally {
      setLoading(false)
    }
    shoots_data = get_shoots(supabase, user);

  }, [user, supabase])

  useEffect(() => {
    getProfile()
  }, [user, getProfile])

  async function updateProfile({ username, website, avatar_url }) {
    try {
      setLoading(true)

      const { error } = await supabase.from('profiles').upsert({
        id: user?.id,
        full_name: fullname,
        username,
        website,
        avatar_url,
        updated_at: new Date().toISOString(),
      })
      if (error) throw error
      alert('Profile updated!')
    } catch (error) {
      alert('Error updating the data!')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div>
      <header className={styles.accountHeader}>
        {<h1 >Hello {fullname}</h1>}
        
        <form action="/auth/signout" method="post" className={styles.signoutform}>
          <button className={styles.signoutbutton} type="submit">
            Sign out
          </button>
        </form>
      </header>
      <div>

        <div className="form-widget">
          <div className={styles.accountElement}>
            <label htmlFor="email">Email</label>
            <input id="email" type="text" value={user?.email} disabled />
          </div>
          <div className={styles.accountElement}>
            <label htmlFor="fullName">Full Name</label>
            <input
              id="fullName"
              type="text"
              value={fullname || ''}
              onChange={(e) => setFullname(e.target.value)}
              />
          </div>
          <div className={styles.accountElement}>
            <label htmlFor="username">Username</label>
            <input
              id="username"
              type="text"
              value={username || ''}
              onChange={(e) => setUsername(e.target.value)}
              />
          </div>
          <div className={styles.accountElement}>
            <label htmlFor="website">Website</label>
            <input
              id="website"
              type="url"
              value={website || ''}
              onChange={(e) => setWebsite(e.target.value)}
              />
          </div>

          <div>
            <button
              className={styles.updatebutton}
              onClick={() => updateProfile({ fullname, username, website, avatar_url })}
              disabled={loading}
              >
              {loading ? 'Loading ...' : 'Update'}
            </button>
          </div>

          <div>

          </div>
        </div>
      </div>


      <div><div>
      <header>
        <h1>Shoots</h1>
      </header>

      {shoots_data? <p>hi</p> : <p>no</p> }


      <div styles="display: flex; flex-direction: column;">
        <form action={insert_shoots}>
          <div className="placeholder">
            <label>Date</label>
            <input id="date" name="date" type="date"></input>
          </div>

          <div className="placeholder">
            <label>time</label>
            <input id="time" name="time" type="time"></input>
          </div>

          <div className="placeholder">
            <label>client</label>
            <input id="client" name="client" type="text"></input>
          </div>

          <div className="placeholder">
            <label>genre</label>
            <input id="genre" name="genre" type="text"></input>
          </div>

          <div className="placeholder">
            <label>editLink</label>
            <input id="editLink" name="editLink" type="url"></input>
          </div>

          <div className="placeholder">
            <label>clientMoodBoardLink</label>
            <input id="clientMoodBoardLink" name="clientMoodBoardLink" type="url"></input>
          </div>

          <div className="placeholder">
            <label>location</label>
            <input id="location" name="location" type="text"></input>
          </div>

          <div className="placeholder">
            <label>notes</label>
            <input id="notes" name="notes" type="text"></input>
          </div>


{/* This is supposed to be an array, so we need to add a button that adds
    more input boxes when ever pressed*/}
          <div className="placeholder">
            <label>contactinfo</label>
            <input id="contactinfo" name="contactinfo" type="text"></input>
          </div>

          <div className="placeholder">
            <label>finishedEditing</label>
            <input id="finishedEditing" name="finishedEditing" type="checkbox"></input>
          </div>

          <div className="placeholder">
            <label>pay</label>
            <input id="pay" name="pay" type="pay"></input>
          </div>

          <div className="placeholder">
            <label>paid</label>
            <input  id="paid" name="paid" type="checkbox"></input>
          </div>
          <button>Submit</button>
        </form>
      </div>
    </div>

  </div>







    </div>
  )
}